import React, { useState, useEffect } from "react";
import { Menu, Form, Container, Button, Card } from "semantic-ui-react";
function Login() {
  const [activeItem, setActiveItem] = useState("register");
  const [Email, setEmail] = useState("");
  const [Password, setpassword] = useState("");
  useEffect(() => {
    console.log(activeItem);
    console.log(Email);
    console.log(Password);
  });
  return (
    <Container textAlign="center">
      <Menu widths="2">
        <Menu.Item
          basic
          color="teal"
          content=" 快速登入"
          active={activeItem === "register"}
          onClick={() => setActiveItem((activeItem) => "register")}
        />
        <Menu.Item
          basic
          color="teal"
          content="  登入"
          active={activeItem === "signin"}
          onClick={() => setActiveItem((activeItem) => "signin")}
        />
      </Menu>
      <Form>
        <Button circular color="facebook" icon="facebook" />
        <Button circular color="google plus" icon="google plus" />

        <Form.Input
          label="Mail"
          value={Email}
          placeholder="請輸入信箱"
          onChange={(e) => setEmail(e.target.value)}
        ></Form.Input>
        <Form.Input
          label="PassWord"
          value={Password}
          placeholder="請輸入密碼"
          onChange={(e) => setpassword(e.target.value)}
          type="password"
        ></Form.Input>
        <Form.Button basic color="teal">
          {activeItem === "register" && "註冊"}
          {activeItem === "signin" && "登入"}
        </Form.Button>
      </Form>
    </Container>
  );
}
export default Login;
